.header {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  color: $white;
  min-height: 62px;
  position: absolute;
  background-color: transparent;
  top: 0;
  right: 0;
  left: 0;
  z-index: 20;

  @media (max-width: $tablet-width) {
    font-weight: normal;
    font-size: 20px;
    line-height: 28px;
    color: $dark;
  }

  &--no-js,
  &--open-menu {
    @media (max-width: $tablet-width) {
      display: block;
      position: static;
      background-color: $white;
      min-height: 568px;
      z-index: 10;
    }
  }

  &--open-menu {
    @media (max-width: $tablet-width) {
      position: absolute;
    }
  }
}

.header__list a {
  color: inherit;
  opacity: 0.7;
  text-decoration: none;
  padding: 10px 0;

  &:hover {
    opacity: 1;
    color: $button;
  }

  &:active {
    opacity: 0.6;
  }
}

.header__nav {
  position: relative;
  max-width: 874px;
  margin: 0 auto;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.header--no-js .header__nav,
.header--open-menu .header__nav {
  @media (max-width: $tablet-width) {
    display: block;
    max-width: 100%;
    margin-top: 100px;
  }
}

.header__list {
  list-style: none;
  margin: 0;
  padding: 20px 20px 9px;
  display: flex;
  column-gap: 20px;
  justify-content: space-between;
  border-bottom: 1px solid $border;
  flex-wrap: wrap;
}

.header--no-js .header__list,
.header--open-menu .header__list {
  @media (max-width: $tablet-width) {
    padding: 0;
    border: none;
    flex-direction: column;
    row-gap: 30px;
  }
}

.header--no-js li,
.header--open-menu li {
  @media (max-width: $tablet-width) {
    background-color: $menu;
    min-height: 60px;
  }
}

.header--no-js a,
.header--open-menu a {
  @media (max-width: $tablet-width) {
    position: relative;
    display: block;
    padding: 15px;
  }

  &::after {
    @media (max-width: $tablet-width) {
      position: absolute;
      content: "";
      width: 25px;
      height: 25px;
      top: 18px;
      right: 0;
      background-image: url("../img/icons/arrow.svg");
      background-repeat: no-repeat;
    }
  }
}

.header__toggle-menu {
  display: none;

  @media (max-width: $tablet-width) {
    display: block;
    position: absolute;
    width: 50px;
    height: 50px;
    top: 0;
    right: 0;
    background-color: $button;
    z-index: 30;
  }

  &::after {
    position: absolute;
    content: "";
    width: 30px;
    height: 30px;
    top: 8px;
    right: 7px;
    background-image: url("../img/icons/menu.svg");
    background-repeat: no-repeat;
  }
}

.header--no-js .header__toggle-menu,
.header--open-menu .header__toggle-menu {
  display: none;
}

.header--open-menu .header__toggle-menu {
  @media (max-width: $tablet-width) {
    display: block;
    background-color: transparent;

    &::after {
      width: 52px;
      height: 52px;
      top: -4px;
      right: -4px;
      background-image: url("../img/icons/close.svg");
    }
  }
}
